<template>
        <CardBoxModal v-if="dashboardToDelete"
          v-model="modalTwoActive"
          title="Confirm deletion"
          button="danger"
          has-cancel
          :on-confirm="deleteDashboard"
        >

        <p>Are you sure you want to delete the dashboard with id {{dashboardToDelete.id}}?       </p>
        <p> <b>Following dashboard will be deleted:</b></p>
        <p>{{dashboardToDelete.json.dashboardName}}</p> 


      </CardBoxModal>
<div>
  <table>
    <thead class="text-xs text-gray-700 uppercase dark:text-gray-400">
      <tr>
        <th />
        <th>Dashboard name</th>
        <th>Coins</th>
        <th>Metrics</th>
        <th>Created</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
        <tr v-for="dashboard in dashboardsPaginated" :key="dashboard.id">
          <th data-label="logo" class="h-6 w-6">
              <div class="h-6 w-6">                        
                <img src="https://cdn4.iconfinder.com/data/icons/business-intelligence-flat/60/Business_Dashboard-intelligence-solutions-512.png" alt="data" width="20" height="20">
              </div>
            </th>
 
                <td data-label="Name">
                    <router-link :to="`/dashboards/${dashboard.id}`">{{ dashboard.json.dashboardName }}</router-link>
                </td>
                <td data-label="Coins" class="lg:w-1 whitespace-nowrap">
                  <ul class="list-disc text-left px-2">
                    <li v-for="coin in dashboard.json.selectedCoins" :key="coin">
                      <small
                    class="text-gray-500 dark:text-slate-400"
                    >{{ coin.name }}</small>
                    </li>
                  </ul>
                </td>
                <td  data-label="Metrics" class="lg:w-1 whitespace-nowrap">
                  <ul class="list-disc text-left px-2">
                    <li v-for="metric in dashboard.json.selectedMetrics" :key="metric">
                      <small
                    class="text-gray-500 dark:text-slate-400"
                    >{{ metric.label }}</small>
                    </li>
                  </ul>
                </td>

                <td data-label="Created" class="lg:w-1 whitespace-nowrap">
                  <small class="text-gray-500 dark:text-slate-400">{{ formatDate(dashboard.created_at) }} </small>
                </td>
                <th data-label="delete" class="h-6 w-6">
                  <div class="h-6 w-6">                        
                    <img src="https://cdn2.iconfinder.com/data/icons/funtime-objects-part-2/60/005_055_delete_cross_close_cancel_exit_vote-512.png" 
                    alt="delete " class="clickable rounded-full" is-hoverable
                    @click="showDeleteModal(dashboard)">
                  </div>
                  
                </th>
      </tr>
    </tbody>
    <tfoot class="text-xs text-gray-700 uppercase dark:text-gray-400">
      <tr>
        <th />
        <th>Dashboard name</th>
        <th>Coins</th>
        <th>Metrics</th>
        <th>Created</th>
        <th></th>
      </tr>
    </tfoot>
  </table>
      <!--PAGINATION--> 
      <div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
        <BaseLevel>
          <BaseButtons>
            <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
              <BaseButton
                class="text-xs text-gray-700 uppercase dark:text-gray-400"
                v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
                :key="page"
                :active="page === currentPage"
                :label="page + 1"
                :color="page === currentPage ? 'lightDark' : 'whiteDark'"
                small
                @click="currentPage = page"
              />
              <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        </BaseButtons>

          <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
        </BaseLevel>
      </div>
</div>
</template>

<script>
import { mdiEye, mdiTrashCan } from "@mdi/js";
import CardBoxModal from "@/components/CardBoxModal.vue";
import CardBox from "@/components/CardBox.vue";
import BaseLevel from "@/components/BaseLevel.vue";
import BaseButtons from "@/components/BaseButtons.vue";
import BaseButton from "@/components/BaseButton.vue";
import UserAvatar from "@/components/UserAvatar.vue";
import { supabase } from "../supabase";


export default {
  components: {
    CardBoxModal,
    CardBox,
    BaseLevel,
    BaseButtons,
    BaseButton,
    UserAvatar,
    mdiEye,
    mdiTrashCan
  },
  data() {
    return {
      dashboardsPaginated: [],
      currentPage: 0,
      perPage: 5,
      modalTwoActive: false,
      dashboardToDelete: null

    };
  },
  props: {
    myDashboards: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  async created () {
    await this.updateDashboardsPaginated();
  },
  watch: {
    currentPage() {
      this.dashboardsPaginated = this.myDashboards.slice(
        this.perPage * this.currentPage,
        this.perPage * (this.currentPage + 1)
      );
    }
  },
  computed: {
    numPages() {
      if (this.myDashboards.length === 0) {
        return 0;
      }
      return Math.ceil(this.myDashboards.length / this.perPage);
    },
    currentPageHuman() {
      return this.currentPage + 1;
    },
    pagesList() {
      const pagesList = [];
      for (let i = 0; i < this.numPages; i++) {
        pagesList.push(i);
      }
      return pagesList;
    },

  },
  methods: {
    showDeleteModal(dashboard) {
      this.dashboardToDelete = dashboard
      this.modalTwoActive = true
    },
    async deleteDashboard() {
      // Make an API call to Supabase to delete the alert with the specified ID
      const { error } = await supabase
        .from("dashboard")
        .delete()
        .eq("id", this.dashboardToDelete.id)
      console.log("delete dashboard", this.dashboardToDelete.id)

      if (error) {
        console.error(error)
      } else {
        location.reload();
      }
    },
    formatDate(date) {
            if (date) {
                return new Date(date).toLocaleString();
            }
            },
    updateDashboardsPaginated() {
      const start = this.perPage * this.currentPage;
      const end = start + this.perPage;
      this.dashboardsPaginated = this.myDashboards.slice(start, end);
    },
  }
};
</script>

<style>
.clickable:hover {
  cursor: pointer;
}
</style>