<template>
    <div>
       ja of nee {{ dataset.percent_change_usd_last_24_hours }}
      <div v-for="(item, key) in dataset" :key="key">
        <b>{{ key }}:</b> {{ item }}
      </div>
    </div>
  </template>
  

<script>
import { supabase } from "../supabase";

export default {
    data() {
        return {
            dataset: {}
        }
    },
    async created() {
        await this.getDataset()
        this.dataset = this.dataset.filter(item => item.symbol === 'GMX')
        this.dataset = this.dataset[0]
    },
    methods: {
        async getDataset() {
            try {
                
                let { data: jsoncoin, error } = await supabase
                .from('jsoncoin')
                .select('payload')
                .order('id', { ascending: false })

                console.log('jsoncoin', jsoncoin)
                this.dataset = jsoncoin[0]['payload']

                if (error) throw error
            } catch (error) {
                console.log('error', error)
            }
        }
    },
}
</script>
