<template>
    
    <div v-if="myDashboards.length > 0">
    <MyDashboardsListTable :myDashboards="myDashboards" />
    
    </div>

    <div v-else>
        <p>No dashboards found</p>
    </div>
</template>

<script>
import MyDashboardsForReal from "@/views/MyDashboardsForReal.vue";
import { supabase, currentUser } from "../supabase";
import MyDashboardsListTable from "./MyDashboardsListTable.vue";


export default {
    components: { MyDashboardsForReal, MyDashboardsListTable },
    data() {
        return {
            user: {},
            myDashboards: {},
        };
    },
    async created() {
        // Fetch data when the component is first created
        await this.getUserInfo();
        await this.fetchData();
    },
    methods: {
        async getUserInfo() {
            // Get currentUser variable from supabase.js and assign it to the user variable
            this.user = currentUser._rawValue;
        },
        async fetchData() {
            // Make HTTP request to Supabase API to get the dashboard data for the user
            try {
                let { data: dashboard, error } = await supabase
                    .from("dashboard")
                    .select()
                    .eq("userid", this.user.id)
                    .order("created_at", { ascending: false });

                this.myDashboards = dashboard;
                console.log("myDashboards ", this.myDashboards);
            }
            catch (error) {
                console.error("Following errors were found: ", error);
                this.errors = error.message;
            }
        },
    },
};
</script>
